.SlideBackground {
    background-color: #212121;
    height: 400px;
}

@media screen and (max-width: 640px) {
    .SlideBackground {
        height: 550px;
    }

    .LandingCarousel {
        font-size: 14px;
    }
}

ul.star-list {
    text-align: left;
    list-style: none;
    margin-left: 0;
    padding-left: 0;
}

ul.star-list > li {
    padding-left: 1em;
    text-indent: -1em;
}

ul.star-list > li:before {
    content: "\2605";
    padding-right: 5px;
}
