.header-bg-img {
    width: 100%;
    height: auto;
}

.profile-img {
    z-index: 2;
    position: relative;
    left: 0;
    right: 0;

    background-image: url('./profile.png');
    border-radius: 100px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    border: 2px solid #fff;
    margin: 0 auto;
    background-color: #fff;

    width: 160px;
    height: 160px;
    top: -80px;
}

@media screen and (max-width: 640px) {
    .profile-img {
        width: 100px;
        height: 100px;
        top: -50px;
    }
}

