.Title {
    line-height: 1.23;
    font-weight: 600;
    font-size: 36px;

    position: relative;
    top: -60px;
}

@media screen and (max-width: 1200px) {
    .Title {
        font-size: 32px;
    }
}

@media screen and (max-width: 640px) {
    .Title {
        font-size: 28px;
        top: -50px;
    }
}
