.Footer {
    padding-top: 150px;
    padding-bottom: 150px;
    padding-left: 20px;
    padding-right: 20px;
    background-color: #212121;

    color: white;
}

@media screen and (max-width: 480px) {
    .Footer {
        padding-top: 90px;
        padding-bottom: 90px;
    }
}

.footer-title, .footer-contacts {
    font-size: 48px;
    font-weight: 600;
    line-height: 1.23;
}

.footer-content {
    margin-top: 45px;
    margin-bottom: 30px;
    
    font-size: 18px;
    font-weight: 300;
    line-height: 1.55;
}

.footer-contacts > * {
    margin: 10px;
}

.footer-contacts > a:hover {
    color: lightgray;
}

@media screen and (max-width: 1200px) {
    .footer-title {
        font-size: 44px;
    }

    .footer-content {
        font-size: 16px;
    }
}

@media screen and (max-width: 640px) {
    .footer-title {
        font-size: 30px;
    }

    .footer-content {
        font-size: 14px;
        line-height: 1.45;
    }
}
