.Content {
    text-align: left;
    font-weight: 300;
    line-height: 1.55;
    
    margin-left: 15%;
    margin-right: 15%;
    margin-bottom: 20px;
    padding: 0;
}

@media screen and (max-width: 1200px) {
    .Content {
        font-size: 18px;
        
        margin-left: 10px;
        margin-right: 10px;
    }
}

@media screen and (max-width: 960px) {
    .Content {
        margin-left: 5px;
        margin-right: 5px;
    }
}
